import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import ReCAPTCHA from "react-google-recaptcha";
import { useTranslation } from "react-i18next";
import Layout from "../layout/layout";
import "./contact.css";

const ContactUs = () => {
  const { t } = useTranslation();
  return (
    <Layout>
      <div className="contact-page">
        <div className="container">
          <div className="contact-title">
            <h2>{t("contact.heading")}</h2>
            <p>{t("contact.paragraph1")}</p>
            <p>{t("contact.paragraph2")}</p>
          </div>
          <div className="contact-form">
            <Form>
              <div className="form-grid">
                <Form.Group className="form-group">
                  <Form.Label>{t("contact.fName")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder={t("contact.fNamePlaceholder")}
                  />
                </Form.Group>
                <Form.Group className="form-group">
                  <Form.Label>{t("contact.lName")}</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder={t("contact.lNamePlaceholder")}
                  />
                </Form.Group>
              </div>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.email")}</Form.Label>
                <Form.Control
                  type="email"
                  placeholder={t("contact.emailPlaceholder")}
                />
              </Form.Group>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.phone")}</Form.Label>
                <Form.Control
                  type="tel"
                  placeholder={t("contact.phonePlaceholder")}
                />
              </Form.Group>
              <Form.Group className="form-group">
                <Form.Label>{t("contact.message")}</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={4}
                  placeholder={t("contact.msgPlaceholder")}
                />
              </Form.Group>
              <ReCAPTCHA sitekey="Your client site key" hl={"en"} />
              <Button className="primary-btn" type="submit">
                {t("contact.button")}
              </Button>
            </Form>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ContactUs;
