/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Header from "../Header/header.jsx";
import Footer from "../Footer/footer.jsx";
import Storage from "../../utils/storage.js";
import en from "../../assets/images/icons/en.svg";
import he from "../../assets/images/icons/he.svg";
import Loader from "../Loader/loader.jsx";

const Layout = ({ children }) => {
  const { i18n } = useTranslation();
  const [currLang, setCurrLang] = useState(null);
  const [loading, setLoading] = useState(false);
  const allLanguages = [
    {
      id: 1,
      name: "עברית",
      shortCode: "he",
      flag: he,
      isRTL: true,
    },
    {
      id: 2,
      name: "English",
      shortCode: "en",
      flag: en,
      isRTL: false,
    },
  ];

  const handleChangeLanguage = (language) => {
    setLoading(true);
    Storage.set("lang", language);
    window.location.reload();
    document.body.classList.remove(language.isRTL ? "ltr" : "rtl");
    document.body.classList.add(language.isRTL ? "rtl" : "ltr");
    setLoading(false);
  };

  const scrollToCosulting = () => {
    let consulting = document.getElementById("consulting-section");
    if (consulting) {
      consulting.scrollIntoView({ top: 0, behavior: "smooth" });
    } else {
      window.location.href = "/#consulting-section";
    }
  };

  const scrollToExperts = () => {
    let experts = document.getElementById("experts-section");
    if (experts) {
      experts.scrollIntoView({ top: 0, behavior: "smooth" });
    } else {
      window.location.href = "/#experts-section";
    }
  };

  const handleCheckLang = () => {
    const currentLanguage = Storage.get("lang");
    if (currentLanguage) {
      setCurrLang(currentLanguage);
    } else {
      Storage.set("lang", allLanguages[0]);
      setCurrLang(allLanguages[0]);
    }
  };

  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    handleCheckLang();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    if (Storage.get("lang").shortCode) {
      i18n.changeLanguage(Storage.get("lang").shortCode);
    }
  }, []);

  if (loading) {
    return <Loader />;
  }
  return (
    <div
      className={`main-layout ${Storage.get("lang")?.isRTL ? "rtl" : "ltr"}`}
      lang={Storage.get("lang")?.shortCode}
      // dir={Storage.get("lang")?.isRTL ? "rtl" : "ltr"}
    >
      <Header
        scrollToCosulting={scrollToCosulting}
        scrollToExperts={scrollToExperts}
        allLanguages={allLanguages}
        handleChangeLanguage={handleChangeLanguage}
        currLang={currLang}
      />
      <div className="content">{children}</div>
      <Footer />
    </div>
  );
};

export default Layout;
