import avtar1 from "../../assets/images/ourExperts/gherta.jpg";
import avtar2 from "../../assets/images/ourExperts/michal.jpg";
import avtar3 from "../../assets/images/ourExperts/amir.jpg";
import avtar4 from "../../assets/images/ourExperts/yosef.jpg";
import avtar5 from "../../assets/images/ourExperts/ayala.jpg";
import avtar6 from "../../assets/images/ourExperts/oswaldo.jpg";
import avtar7 from "../../assets/images/ourExperts/anna.jpg";
import avtar8 from "../../assets/images/ourExperts/efrat.jpeg";
import avtar9 from "../../assets/images/ourExperts/yitzhak.jpg";
import { Link } from "react-router-dom";

const OurExpert = () => {
  const ourExpertList = [
    {
      id: 1,
      docName: "Dr. Greta Brill",
      fieldName: "Pediatric Endocrinology",
      avtar: avtar1,
    },
    {
      id: 2,
      docName: "Dr. Michal Dviri",
      fieldName: "Women newborn fertility gynecology",
      avtar: avtar2,
    },
    {
      id: 3,
      docName: "Dr. Amir Weintraub",
      fieldName: "fertility",
      avtar: avtar3,
    },
    {
      id: 4,
      docName: "Prof. Yosef Rosenman",
      fieldName: "Cardiology",
      avtar: avtar4,
    },
    {
      id: 5,
      docName: "Prof. Ayala Polak",
      fieldName: "Ophthalmology",
      avtar: avtar5,
    },

    {
      id: 6,
      docName: "Dr. Oswaldo-Meir Rabinovitch",
      fieldName: "gynecology",
      avtar: avtar6,
    },
    { id: 7, docName: "Dr. Anna Gluhov", fieldName: "Sexology", avtar: avtar7 },
    {
      id: 8,
      docName: "Dr. Efrat Solomon Cohen",
      fieldName: "Dermatology",
      avtar: avtar8,
    },
    {
      id: 9,
      docName: "Yitzhak Ziv-Ner",
      fieldName: "Orthopedic rehabilitation",
      avtar: avtar9,
    },
  ];
  return (
    <div className="our-expert" id="experts-section">
      <div className="container">
        <div className="our-expert-title">
          <h2>The leading expert doctors in every field</h2>
          <p>
            A network of carefully selected specialist doctors are available for
            a call at the click of a button.{" "}
          </p>
          <p>
            The experts who work with hidoc are doctors licensed to practice
            medicine in their fields of expertise in Israel.
          </p>
        </div>
        <div className="our-expert-grid">
          {ourExpertList.map((el, i) => (
            <div className="our-expert-box" key={i}>
              <img src={el.avtar} alt="avtar" />
              <div className="our-expert-content">
                <h6>{el.docName}</h6>
                <p>{el.fieldName}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="appointment-btn">
          <Link to="/https://beta.hidoc.co.il/" className="secondary-btn">
            Summoning a conversation with a doctor
          </Link>
        </div>
      </div>
    </div>
  );
};

export default OurExpert;
