import React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import "./footer.css";
import {
  FacebookIconSVG,
  InstagramIconSVG,
  LinkedinIconSVG,
  LogoFile,
  OpinionSVG,
} from "./footerIconsSVG";

const Footer = () => {
  const { t } = useTranslation();
  return (
    <div className="footer">
      <div className="container">
        <div className="top-footer">
          <div className="footer-left-col">
            <div className="footer-logo">
              <Link to="/">{LogoFile}</Link>
            </div>

            <p className="address">
              {t("footer.address1")}
              <br />
              {t("footer.address2")}
            </p>

            <div className="footer-social">
              <ul>
                <li>
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href="https://www.facebook.com/hidocisrael"
                  >
                    {FacebookIconSVG}
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href="https://www.linkedin.com/company/37465712"
                  >
                    {LinkedinIconSVG}
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href="https://www.instagram.com/israelhidoc"
                  >
                    {InstagramIconSVG}
                  </a>
                </li>
              </ul>
            </div>
          </div>

          <div className="footer-menu">
            <div className="footer-links-section">
              <ul className="links-list">
                <li>
                  <Link to="/privacy"> {t("footer.privacy")}</Link>
                </li>
                <li>
                  <Link to="/terms"> {t("footer.terms")} </Link>
                </li>
                <li>
                  <a href="https://hidoc.co.il/medaat">
                    {" "}
                    {t("footer.informed")}
                  </a>
                </li>
                <li>
                  <a href="https://hidoc.co.il/accessibility">
                    {t("footer.accesability")}
                  </a>
                </li>
                <li>
                  <a href="https://madanes.com/"> {t("footer.madness")}</a>
                </li>
              </ul>
              <ul className="links-list">
                <li>
                  <Link to="/about"> {t("footer.about")}</Link>
                </li>
                <li>
                  <Link to="/DoctorRegistration">{t("footer.joinUs")}</Link>
                </li>
                <li>
                  <a href="https://hidoc.co.il/faq_clients">{t("footer.qa")}</a>
                </li>
                <li>
                  <Link to="/contact"> {t("header.contact")}</Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div className="container">
        <div className="bottom-footer">
          <div className="footer-grid">
            <div className="powered-by">
              <p>
                {t("footer.poweredBy")} {OpinionSVG}
              </p>
              <p> {t("footer.copyright")}</p>
            </div>
          </div>

          <div className="footer-grid footer-note">
            <div className="note-section">
              <p>{t("footer.note")}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
