import Layout from "../layout/layout";
import "./about.css";

const AboutUs = () => {
  return (
    <Layout>
      <div className="about">
        <div className="container">
          <h2>About Us</h2>
          <div className="about-content">
            <div className="about-video">
              {/* <iframe
                class="learnmore-video-iframe"
                id="youtube-video"
                width="100%"
                height="500px"
                src="https://www.youtube.com/embed/y2bKeq296Vg"
                title="YouTube video player"
                frameborder="0"
                allow="accelerometer; autoplay; muted; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen
                muted
              ></iframe> */}
              <iframe
                width="100%"
                height="500px"
                class="about-video-iframe"
                src="https://www.youtube.com/embed/tAnjsCj1y8Q"
                title="YouTube video player"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              ></iframe>
            </div>
            <div className="about-description">
              <h1>HIDOC the leading private internet clinic in Israel</h1>
              <p>
                HIDOC is an innovative venture whose goal is to make the field
                of private medicine in Israel accessible, through a telemedicine
                platform.
              </p>
              <p>
                HIDOC's goal is to allow users convenient and easy access to
                specialist doctors for diagnostic purposes, receiving medical
                treatment and advice, and all in a safe and easy-to-use video
                call.
              </p>
              <p>
                Using HIDOC you can send specialist doctors messages, upload
                files (such as laboratory tests, imaging files, etc.) and ask
                any question, in an easy, fast and simple way.
              </p>
              <p>
                Our team of experts have been carefully selected to ensure
                quality, professional and efficient advice.
              </p>
              <p>
                We invite you to use HIDOC services and ensure your health in an
                easy, safe and convenient way.
              </p>
              <p>at your service always,</p>
              <p>
                <b>HIDOC team </b>
              </p>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default AboutUs;
