import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import icon1 from "../../assets/images/icons/step1_icon.png";
import icon2 from "../../assets/images/icons/step2_icon.png";
import icon3 from "../../assets/images/icons/step3_icon.png";

const AppointmentSection = () => {
  const { t } = useTranslation();
  return (
    <div className="appointment-section">
      <div className="container">
        <div className="appointment-section-steps">
          <div className="appointment-title">
            <h2>{t("appointment.heading")}</h2>
            <h3>{t("appointment.subHeading")}</h3>
          </div>
          <div className="appointment-steps">
            <div className="appointment-steps-grid">
              <div className="appointment-steps-img">
                <img src={icon1} alt="icon" />
              </div>
              <div className="appointment-steps-text">
                <h2>{t("appointment.step1heading")}</h2>
                <p>{t("appointment.list1")}</p>
                <p>{t("appointment.list2")}</p>
                <p>{t("appointment.list3")}</p>
              </div>
            </div>
            <div className="appointment-steps-grid">
              <div className="appointment-steps-img">
                <img src={icon2} alt="icon" />
              </div>
              <div className="appointment-steps-text">
                <h2>{t("appointment.step2heading")}</h2>
                <p>{t("appointment.list4")}</p>
                <p>{t("appointment.list5")}</p>
                <p>{t("appointment.list6")}</p>
                <p>{t("appointment.list7")}</p>
              </div>
            </div>
            <div className="appointment-steps-grid">
              <div className="appointment-steps-img">
                <img src={icon3} alt="icon" />
              </div>
              <div className="appointment-steps-text">
                <h2>{t("appointment.step3heading")}</h2>
                <p>{t("appointment.list8")}</p>
                <p>{t("appointment.list9")}</p>
              </div>
            </div>

            <div className="appointment-btn">
              <Link to="/https://beta.hidoc.co.il/" className="secondary-btn">
                {t("homeBanner.button")}
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AppointmentSection;
