import { Link } from "react-router-dom";
import Layout from "../layout/layout";
import "./terms.css";

const PrivacyPolicy = () => {
  return (
    <Layout>
      <div className="terms-pages">
        <div className="container">
          <h2>Privacy Policy</h2>
          <div className="terms-grid">
            <p>Last updated: March 1, 2021</p>
            <h3>1. General</h3>
            <p>
              Best Med Opionion Ltd. ("Best Med") Valor Smart Products Ltd.
              ("Valor") (collectively, "Best Med Valor" or the "Company").offer
              a digital solution for remote medicine (telemedicine) through
              their platform, Hidoc.For the purpose of providing the services,
              offering and publishing them, the company operates a website (the
              "Site") and a variety of other means, including, among other
              things, advanced technological means, which enable communication
              with the users of the site, including registered users.
            </p>
            <p>
              The privacy policy detailed here describes, among other things,
              how the company collects and processes personal information, what
              is the personal information that the company collects, what are
              the uses that are made of it and what are the rights of the data
              subjects in relation to said information.
            </p>
            <p>
              This policy is worded in the masculine gender for convenience
              only, but applies to all genders.The terms in this policy, which
              were not explicitly defined within it, will be interpreted in
              accordance with the definitions given to them in the terms of use,
              located at <Link to="https://hidoc.co.il/">www.hidoc.co.il</Link>
            </p>
            <p>
              Agreeing to the privacy policy is a condition for receiving the
              services. Registering for the services constitutes consent to the
              company's privacy policy.
            </p>
            <h3>2. The information collected by the company</h3>
            <ol>
              <li>
                When using the website, the company may collect information
                about your practices in relation to the use of the website,
                including content you have loaded or published, information you
                have read, the pages you have viewed, the services you have been
                interested in, the location of the computer and the internet
                address data (IP address) through which you access the website,
                the identification number of the smartphone used you, the type
                of operating system you use, the type of end device you have and
                more.
              </li>
              <li>
                In order to register on the website and receive services, you
                will be asked to provide your name, your phone number and an
                e-mail address that you will use in connection with the
                services. We will also record in our records the medical
                services you consumed, the medical problem you suffer from, the
                questionnaire you filled out, the medical documents you
                uploaded, the medical imaging you uploaded, the documentation of
                the medical encounter by the doctor and the identity of the
                doctor who provided these services. Also, you may register to
                receive a service for minors and juvenile delinquents if you are
                their parent or legal guardian, in which case we will collect
                the above details regarding the minors or juvenile delinquents.
              </li>
              <li>
                Doctors who use the site provide the following information:
                Name, contact details, professional training and specialization,
                disciplinary history, existence of insurance coverage.
              </li>
              <li>
                In order to pay for the services, you will be transferred to the
                website of a clearing company and will be asked to provide the
                details required to make a payment. These details will not be
                given to us nor will they be kept by us.
              </li>
              <li>
                To the extent that the site includes references to other sites
                or applications (including, among others, the clearing site),
                the use of these sites is not under our control or
                responsibility and is subject to the terms of use and the
                privacy policy of each site.
              </li>
            </ol>
            <p>
              There is no legal obligation for you to provide the information,
              and the provision of the information depends on your will and
              consent.
            </p>
            <p>
              In addition, the company may collect and use the services of third
              parties to collect and analyze anonymous, statistical, or
              aggregate (aggregate) information in connection with the use of
              the website, including information regarding your activity on the
              website ("statistical information"). Statistical information helps
              us understand trends and the needs of users in order to So that we
              can consider new services and products and so that we can adapt
              existing services and products to the needs of users. We will not
              link statistical information to personal information. We may
              provide statistical information to our partners, without
              limitation, according to commercial terms determined at our sole
              discretion.
            </p>
            <h3>3. The processing of personal information by the company</h3>
            <p>
              The information will be processed in accordance with the
              requirements of the Privacy Protection Law (1981 - 1981) ("the
              law"), the regulations established by virtue of the law, and the
              guidelines of the Privacy Protection Authority ("Privacy
              Protection Laws").
            </p>
            <h3>4. Use of personal information</h3>
            <p>
              The use of the collected information will be done according to
              this privacy policy, in accordance with the privacy protection
              laws.
            </p>
            <p>
              The company will use the personal information collected for the
              following purposes:
            </p>
            <ol>
              <li>Provision of the services.</li>
              <li>
                Compliance with the provisions of privacy protection laws and
                any other law.
              </li>
              <li>Identity verification of applicants to the company.</li>
              <li>
                Diagnosing problems or improving and enriching the content and
                services offered by the company.
              </li>
              <li>Statistic analysis.</li>
              <li>
                Management of legal proceedings concerning the subject of
                information.
              </li>
              <li>Fraud prevention.</li>
              <li>Securing.</li>
              <li>Risk management.</li>
              <li>
                Marketing needs, including publishing information and content.
              </li>
            </ol>
            <h3>5. Security of personal information</h3>
            <p>
              The company invests a lot of resources in order to secure the
              information, and is authorized in accordance with the provisions
              of the applicable law.
            </p>
            <p>
              The company uses industry standard measures to protect the
              information provided to it, both at the time of delivery and after
              it receives it.{" "}
            </p>
            <p>
              However, there are no methods of electronic transfer or storage
              that are completely secure.{" "}
            </p>
            <p>
              Therefore, although the company tries to take acceptable ways to
              protect your information, the company cannot guarantee the
              absolute security of the information.
            </p>
            <p>
              The company will delete any personal information it received from
              a user after receiving a written request from that user. The
              company cannot recover deleted information.{" "}
            </p>
            <p>
              There are limitations in maintaining privacy and medical
              confidentiality, and there may even be incidents of communication
              failure and sudden disconnection between you and the service. The
              company does its best to secure the information provided by you.It
              is your sole responsibility to ensure that your privacy is
              maintained in relation to personal medical information that
              appears on the personal digital display from which the service
              call is made (for example: computer screen, terminal, telephone,
              etc.) (which may be exposed to people in your environment).
              Contacting the service through non-secure communication can be
              exposed on the Internet. The company will guarantee as much as it
              can the privacy of the information stored with it, but it cannot
              guarantee the privacy of the information during the connection
              from the endpoint you are using and the information stored at the
              endpoint with you. Therefore, at the end of each use, it is
              recommended to remove the information and/or the identification
              data, including username and password or any other data from the
              endpoint you used, in order to reduce the possibilities of
              accessing information about you and/or the service to those who
              are not authorized by you. The security of the information stored
              on your end point is your sole responsibility and the company has
              no ability to secure this information. Also, the use of electronic
              communication and the storage of information in digital media are
              exposed to cyber-attacks, or information leaks, and you confirm
              that you are aware that personal and/or medical information that
              will be transferred by you and/or to you as part of the medical
              treatment and/or in connection with it may be exposed as a result.
              The use of electronic communication and storage of information in
              digital media may also result in a partial or complete loss of the
              information as well as the termination of the service.
            </p>
            <h3>
              6. Sources of personal information The company collects personal
              information from several main sources:
            </h3>
            <ol>
              <li> When the user uses the website, and/or the services. </li>
              <li>
                Information provided for the purpose of receiving services
                and/or information from the company.{" "}
              </li>
              <li> From suppliers and contractors of the company. </li>
              <li>
                From the doctors who use the website and provide medical
                services to users.
              </li>
            </ol>
            <h3>7. Saving personal information</h3>
            <p>
              The personal information will be stored in the company's database,
              in accordance with the provisions of the Privacy Protection Law,
              and will be used according to the provisions of this privacy
              policy and/or according to the provisions of any law. Subject to
              the provisions of any law and the circumstances relating to the
              provision of the services, we will refrain from keeping the
              information for a period that exceeds what is required.
            </p>
            <h3>8. Handing over personal information to a third party</h3>
            <p>
              The company will be careful to observe the provisions of the
              privacy protection laws, and will not disclose personal
              information to anyone who is not authorized to do so by law. At
              the same time, personal information will be transferred to third
              parties, if necessary, as detailed below:
            </p>
            <ol>
              <li>
                With the consent of the subject of the information or notice to
                the subject of the information as required by law.
              </li>
              <li>
                {" "}
                as required in connection with the provision of the services.
                For example, the attending physician has access to information
                about his patients and a medical official entrusted with
                professional supervision has the option of accessing the medical
                information required for supervision.
              </li>
              <li>
                At the request of parties to whom there is an obligation by law
                to provide them with information.
              </li>
              <li>
                In accordance with the provisions of a court order or any other
                competent authority or as part of legal proceedings, if any,
                between the subject of the information and the company.
              </li>
              <li>
                In the event that the company believes that the transfer of the
                information is necessary to prevent serious damage to its body
                or property, or that of anyone else.
              </li>
              <li>
                In the event of dissolution and/or merger of the company's
                activities (in whole or in part) with any third party and/or
                transfer of the company's activities (in whole or in part) to a
                third party, and/or to a body to which the company assigns the
                rights granted to it by virtue of these instructions (all or
                some of them), including during negotiations in connection
                therewith and including a transfer outside of Israel. In the
                aforementioned case, the company will take reasonable efforts to
                ensure the continued protection of the privacy of the
                information.
              </li>
              <li>
                To companies related to the company or to a third party that
                provides services to the company (such as providers of
                information storage services in the cloud, computing and
                information technologies). The company will require its
                affiliated companies, suppliers and/or subcontractors to act
                according to its privacy protection procedures.We may use the
                services of subcontractors located in countries outside of
                Israel, and send them the information we receive. Some countries
                may have levels of protection for personal or other information
                that are not comparable to the level of protection in your
                country.
              </li>
              <li>
                Business partners with whom the company may jointly offer
                products or services, or whose products or services will be
                offered on the company's website. You will know when a third
                party is associated with a product or service offer when its
                name appears in the offer, alone or together with the company's
                name.If you choose to use these products or services, the
                Company may share information about you, including your personal
                information, with those partners. Please note that the Company
                does not control the privacy practices of these business
                partners. If you no longer wish to allow a third party to use
                your information, contact that third party.
              </li>
              <li>
                If you were referred to this site from another site (for
                example, through a link), the Company may share certain
                information about you with the referring site. The company
                encourages you to review the privacy policy of each website that
                referred you to the company's website. In addition, when you go
                to the website of the company's partners through a link from the
                company's website, the company provides this partner with a
                unique identifier, so that he knows that you were directed to
                him from the company's website. This unique identifier will not
                contain your personal information.
              </li>
            </ol>
            <h3>9. Review of personal information</h3>
            <p>
              According to the Privacy Protection Law, 1981-1981, every person
              has the right to review personal information about him held in a
              database.
            </p>
            <p>
              As long as there is no legal impediment to this, the company will
              allow you to review personal information about you held by it in
              the database, after submitting a written request as required by
              privacy protection laws.
            </p>
            <p>
              If you found that information about you is not complete, correct,
              clear or updated, you may contact the company with a request to
              correct the information or delete it.
            </p>
            <p>
              Requests to exercise the right of review and correction, as
              specified above, must be sent via the{" "}
              <Link to="/contact">contact</Link> page.
            </p>
            <p>
              The Company will retain your information as long as your account
              is active or as necessary to provide you with services. If you
              wish to close your account or request that we not use your
              information to provide you services, contact us at the link above.
              We will retain your information and use it as necessary to comply
              with our legal obligations, resolve disputes and enforce our
              agreements.
            </p>
            <h3>10. Use of the website by children</h3>
            <p>
              The company does not collect identifying information from children
              under the age of 13. In the event that you believe that a child
              under the age of 13 has registered without the consent of their
              parents or guardian, please notify us immediately.
            </p>
            <h3>11. Electronic mailing</h3>
            <p>
              The company will be entitled to send from time to time, by e-mail,
              or through communication details provided as part of registration
              and/or interest in the services, advertisements for products or
              services provided to the applicant in the past or for similar or
              complementary products.
            </p>
            <p>
              The subject of the information has the right to notify at any time
              of his refusal to receive such advertisement. For your
              information, we reserve the right to send you messages related to
              the Services, including service messages and administrative
              messages, relating to your account, without offering you the
              option to cancel receiving them, unless you close your account.
            </p>
            <h3>12. Cookies</h3>
            <p>
              A cookie is a short piece of text that is sent to the browser of
              the website visitor. On a return visit, the browser sends this
              fragment to the original visitor's device. The company may use
              cookie technology and essentially similar technologies for the
              ongoing and proper operation of the website (hereafter
              collectively "cookies"), including to collect statistical data
              about the use of the website, to verify details, to adapt the
              website to your personal preferences and information security
              needs. We may use cookies
            </p>
            <p>"Persistent" to save your settings and customizations.</p>
            <p>
              Browsing the company's website and using the services included in
              it constitutes consent on your part that the company will be able
              to carry out computerized supervision of the browsing patterns and
              the use you make of the services through "cookies" and other
              similar means.
            </p>
            <p>
              The company may use any such information, including personal
              information, for the purpose of control and/or supervision and/or
              development and/or maintenance of the quality of the services,
              their level and integrity and/or for the purpose of complying with
              the provisions of any law.
            </p>
            <p>
              Most browsers include an option to avoid receiving cookies. If you
              don't know how to do this, check the help file of the browser you
              are using. In some cases, failure to grant such permission may
              harm the quality of the service provided by the company.
            </p>
            <h3>13. Changes to the privacy policy</h3>
            <p>
              The company reserves the right to change the provisions of this
              privacy policy from time to time. If changes are made to this
              policy
            </p>
            <p>
              material, a notice will be published on the company's website.
            </p>
            <h3>14. Contact</h3>
            <p>
              For any question or problem related to the privacy policy, please
              contact: <Link to="/contact">https://hidoc.co.il/contact</Link>{" "}
            </p>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default PrivacyPolicy;
