import { useEffect, useState } from "react";
import { Navbar, Nav, Dropdown } from "react-bootstrap";
import { Link, NavLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import logo from "../../assets/images/logo/logo_blue.png";
import whitelogo from "../../assets/images/logo/logo_white.png";
import blueImg from "../../assets/images/header/top_left.png";
import whiteImg from "../../assets/images/header/top_left_no_bg.png";
import "./header.css";

const Header = ({
  scrollToExperts,
  scrollToCosulting,
  allLanguages,
  handleChangeLanguage,
  currLang,
}) => {
  const { t } = useTranslation();
  const [topValue, setTopValue] = useState(0);
  const path = window.location.pathname;

  const handleScroll = () => {
    setTopValue(window.scrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <div className="header-wrapper">
        <div className="header">
          <div className="header-grid">
            <div className="logo-box">
              <Link to="/" className="logo">
                <img src={logo} alt="desktop-logo" className="desk-view" />
                <img
                  src={whitelogo}
                  alt="mobile-logo"
                  className="mobile-view"
                />
              </Link>
              <div className="logo-bg">
                <img src={whiteImg} alt="whiteImg" />
                {path === "/"
                  ? topValue >= 190 && (
                      <img src={blueImg} alt="blueImg" className="blue-img" />
                    )
                  : topValue >= 0 && (
                      <img src={blueImg} alt="blueImg" className="blue-img" />
                    )}
              </div>
            </div>

            <div className="header-menu-section">
              <div className="header-menu">
                <Navbar expand="lg">
                  <Navbar.Toggle aria-controls="basic-navbar-nav" />
                  <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav>
                      <NavLink className="nav-link" to="/">
                        <div className="mobile-view-logo">
                          <img src={whitelogo} alt="mobile-logo" />
                        </div>
                      </NavLink>
                      <NavLink className="nav-link" to="/">
                        {t("header.home")}
                      </NavLink>

                      <NavLink className="nav-link mobile-view">
                        <span onClick={scrollToCosulting}>
                          {t("header.consulting")}
                        </span>
                      </NavLink>
                      <NavLink className="nav-link mobile-view">
                        <span onClick={scrollToExperts}>
                          {t("header.expert")}
                        </span>
                      </NavLink>
                      <NavLink className="nav-link" to="/contact">
                        {t("header.contact")}
                      </NavLink>
                    </Nav>
                  </Navbar.Collapse>
                </Navbar>
              </div>
            </div>
            <div className="auth-box">
              <Link to="https://beta.hidoc.co.il/">{t("header.connect")}</Link>

              <Dropdown className="lang-dropdown">
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  {currLang?.shortCode}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                  {allLanguages
                    .filter((l) => l.shortCode !== currLang?.shortCode)
                    .map((lang, index) => (
                      <Dropdown.Item
                        onClick={() => handleChangeLanguage(lang)}
                        key={lang?.id}
                        as="button"
                      >
                        <img src={lang.flag} alt="icon" /> {lang.name}
                      </Dropdown.Item>
                    ))}
                </Dropdown.Menu>
              </Dropdown>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;
